import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom';

import uxProjects from './uxProjects';
import uxDetail from './uxDetail';
import webProjects from './webProjects';
import webDetail from './webDetail';
import Home from './Home';
import Contact from './Contact';
import Header from './Header';
import Footer from './Footer';
import './style.css';


class App extends React.Component {
    render () {
        return (
            <BrowserRouter>
                <div className="portfolio-body">
                <Header />
                <Route path="/" exact component={Home} />
                <Route path="/user-experience" exact component={uxProjects} />
                <Route path="/user-experience" exact component={uxDetail} />
                <Route path="/web-development" exact component={webProjects} />
                <Route path="/web-development" exact component={webDetail} />
                <Route path="/contact-me" exact component={Contact} />
                
                <a className="ui container jump-top" href="#"><h4><i className="angle double up icon"></i></h4></a>
                <Footer />
                </div>
            </BrowserRouter>
        );
    }
}

export default App;