import React, { Component } from 'react';
import { connect } from 'react-redux';
import { selectProject } from '../actions';
import './style.css';

class uxProjects extends Component {
    renderList() {
        return this.props.project.map((project) => {
            return (
                <div className="column" key={project.title}>
                    <div className="project-select"> 
                        <button 
                            onClick={() => this.props.selectProject(project)}
                            className="medium ui theme-orange button">
                            {project.title}
                        </button>
                    </div>
                </div>
            );
        });
    }

    render() {
        return (
            <div className="project-body ui container center aligned">
                <h1 className="ui horizontal divider header text container">UX PROJECTS</h1>
                <div className="ui center aligned three column doubling stackable grid">
                    {this.renderList()}
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {project: state.uxProject};
}

export default connect(
    mapStateToProps, 
    { selectProject }
)(uxProjects);
