import React from 'react';
import { connect } from 'react-redux';
import './style.css';

const uxDetail = ({ project }) => {
    if(!project){
        return <div>Select a Project</div>
    }
    if(project.title === 'UX LAB') {
       return (
            <div className="project-details ui text container">
                <h1>UX Lab</h1>
                <p>{project.description}</p>
                <p>{project.descriptionTwo}</p>
                <p>{project.descriptionThree}</p>
                <p>{project.descriptionFour}</p>
            </div>
        );
    }
    if(project.title === 'Donors to Donations') {
        return (
             <div className="project-details ui text container">
                 <h1>{project.title}</h1>
                 <h3>{project.languages}</h3>
                 <p>{project.description}</p>
                 <p>{project.descriptionTwo}</p>
                 <p>{project.descriptionThree}</p> 
                 <button className="ui button theme-orange">
                 <a target="_blank" rel="noopener noreferrer" className="project-link" 
                        href={project.link}>See our Presentation</a>
                </button>
             </div>
         );
     } else {
        return (
            <div className="project-details ui text container">
                <h1>Donors to Donations</h1>
                <h3>Tools used: Microsoft Axure, R Studio</h3>
                <p>Donors to Donations was a course project for the upper level HCI course at UBC. Our project won ‘The most polished project’ 
                at the year-end showcase.</p>
                <p>I conducted field research to help refine our project idea by speaking to nonprofit organizations, such as Ronald McDonald. 
                We refined our project goals and created 2 prototypes to see what website design would be the most effective to
                 encourage users to donate to a meaningful charity.</p>
                <p>I helped create one of the two prototypes using Microsoft Axure, 
                        with both breadth and depth of functionality. 
                        Our team of 5 each conducted usability tests with these two prototypes. 
                        Our project design was a 2-way within subject design, and we measured number of errors,
                         searches, and satisfaction rating of the user as our quantitative data. 
                         The quantitative and qualitative data was then analyzed using R.</p> 
                <button className="ui button theme-orange">
                    <a target="_blank" rel="noopener noreferrer" className="project-link" 
                            href="https://docs.google.com/presentation/d/1IKgm0q598-m5GZOZJ3CwnXGctyEUhmhEDhyGlirrFKc/edit?usp=sharing">See our Presentation</a>
                </button>
            </div>
        );  
     }
    
};

const mapStateToProps = (state) => {
    return { project: state.uxProjectSelected }
};

export default connect(mapStateToProps)(uxDetail);    