import React from 'react';

class Contact extends React.Component {
    render(){
        return (
            <div className="ui container">
                <div className="project-body ui center aligned">
                    <h1 className="ui horizontal divider header text container">GET IN TOUCH</h1>
                </div>
                <div className="welcome-home ui text container">
                    <p><i className="icon linkedin"></i>
                        <a className="linkedin" href="https://www.linkedin.com/in/cynthia-deng/" 
                            rel="noopener noreferrer" target="_blank" >Connect with me on LinkedIn! 
                        </a>
                    </p>
                    <p><i className="icon mail"></i>Email at cynthiad215@gmail.com </p>
                </div>
                <div className="project-details"></div>
            </div>      
        );
    };
}

export default Contact;