import React from 'react';
import './style.css';
import headshot from '../images/headshot.jpg';
class Home extends React.Component {
    render(){
        return (
            <div className="ui container">
                <div className="project-body ui center aligned">
                    <h1 className="ui horizontal divider header text container">WELCOME</h1>
                </div>
                <div className="welcome-home ui text container">
                    <div className="ui container center aligned">
                        <img src={headshot} alt="headshot_cn tower background" className="image"/>
                    </div>
                    
                    <p className="intro-para ">
                        Hi! I'm Cynthia and a recent graduate of UBC with a major in Cognitive Systems, 
                        Computational Intelligence and Design.</p>
                    
                </div>
            </div>      
        );
    };
}

export default Home;