import React, { Component } from 'react';
import { connect } from 'react-redux';
import { selectProject } from '../actions';
import './style.css';

class webProjects extends Component {
    renderList() {
        return this.props.project.map((project) => {
            return (
                <div className="column" key={project.title}>
                    <div className="project-select"> 
                        <button 
                            onClick={() => this.props.selectProject(project)}
                            className="medium ui theme-orange button">
                            {project.title}
                        </button>
                    </div>
                </div>
            );
        });
    }

    render() {
        return (
            <div className="project-body ui container center aligned">
                <h1 className="ui horizontal divider header text container">WEB DEVELOPMENT</h1>
                <div className="ui stackable doubling four column grid center aligned">
                    {this.renderList()}
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {project: state.webProject, chosen: state.webProjectSelected};
}

export default connect(
    mapStateToProps, 
    { selectProject }
    )(webProjects);
