import React from 'react';
import { connect } from 'react-redux';
import './style.css';

const webDetail = ({ project }) => {
    if(!project){
        return <div>Select a Project</div>
    }
    if(project.title === "Portfolio"){
        return (
        <div className="project-details ui text container">
            <h1 className="center aligned">{project.title}</h1>
            <h3>{project.languages}</h3>
            <p>{project.description}</p>
        </div>);
    }
    if(project.title === "LumoHacks 2018"){
        return (
        <div className="project-details ui text container">
            <h1 className="center aligned">{project.title}</h1>
            <h3>{project.languages}</h3>
            <p>{project.description}</p>
            <button className="ui button theme-orange"><a target="_blank" rel="noopener noreferrer" className="project-link" href={project.link}>See Project</a></button>
        </div>);
    }
    if (project.title === 'UBC Brand & Marketing'){
        return (
            <div className="project-details ui text container">
                <h1 className="center aligned">{project.title}</h1>
                <h2>Webpage Redesigns</h2>
                <h3>{project.webRedesigns.languages}</h3>
                <p>{project.webRedesigns.description}</p>
                <button className="ui button theme-orange">
                    <a target="_blank"  rel="noopener noreferrer" className="project-link" href={project.webRedesigns.eventLink}>
                        See Events Website
                    </a>
                </button>
                <button className="ui button theme-orange">
                    <a rel="noopener noreferrer" target="_blank" className="project-link" href={project.webRedesigns.robsonLink}>
                        See Robson Square Website
                    </a>
                </button>
                <hr/>
                <h2>UBC Homepage Archive</h2>
                <h3>{project.vueArchive.languages}</h3>
                <p>{project.vueArchive.description}</p>
                <p>{project.vueArchive.descriptionTwo}</p>
                <button className="ui button theme-orange">
                    <a target="_blank" rel="noopener noreferrer" className="project-link" href={project.vueArchive.link}>
                        See the archive!
                    </a>
                </button>
               <hr />
               <h2>Post Meta WordPress Plugin</h2>
                <h3>{project.restPlugin.languages}</h3>
                <p>{project.restPlugin.description}</p>
                <button className="ui button theme-orange">
                    <a target="_blank" rel="noopener noreferrer"className="project-link" href={project.restPlugin.link}>
                        See Plugin
                    </a>
                </button>
                
        </div>
        );
    } else {
        return (
            <div className="project-details ui text container">
                <h1>Portfolio</h1>
                <h3>Languages and Tools used: React, Redux, GitHub </h3>
                <p>This portfolio was built using react JS, react router and redux. It is still very much a work in progress, stay tuned for more updates and jazz in the near future!</p>
            </div>
        );
    }
    
};

const mapStateToProps = (state) => {
    return { project: state.webProjectSelected }
};

export default connect(mapStateToProps)(webDetail);    