const webProjectSelected = (webProjectSelected=INITIAL_STATE_WEB, action) => {
    if(action.type === 'PROJECT_SELECTED') {
        return action.payload;
    }
    return webProjectSelected;
}

export default combineReducers({
    uxProject: uxProjectsReducer,
    uxProjectSelected: uxProjectSelected,
    webProject: webProjectsReducer,
    webProjectSelected: webProjectSelected,

});